
input[type=text],[type=email],select {
    margin-top: 33px;
    height: 50px;
    width: 617px;
    border-radius: 10px;
    padding: 12px;
    border: none;
    outline: none;
   
    
}

.invalid{
    border: 2px solid red !important;
    background-image: url(../../../assets/img/invalid.png);
    background-position:right 12px top 12px ;
    background-repeat: no-repeat;

}
.valid{
    border: 3px solid greenyellow !important;
    background-image: url(../../../assets/img/valid.png) !important;
    background-position:right 12px top 12px;
    background-repeat: no-repeat;


}

.input_flex{
    display: flex;

}

#contact{
      overflow-x: hidden;
}
#message{
    margin-top: 33px;
    height: 340px;
    width: 617px;
    border-radius: 10px;
    padding: 12px;
    border: none;
    outline: none;

}

.mail_send{
    display: none;
}

.email_send_true{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px;
    height: 2px;
    width: 220px;
    border-radius: 2444px;
    font-size: 24px;
    background-color: #4AD592;
    border: white;
    text-shadow: 1px 1px 4px black;
    color:whitesmoke;
    font-family: 'Montserrat';
}

.sendmail_button{
    color: #4AD592;
    border: 2px solid #4AD592;
    border-radius: 7px;
    height: 58px;
    width: 254px;
    background-color: #0F0F0F;

    transition: background-color 1.1s;

    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background: linear-gradient(to top left, transparent 50%, #4AD592 50%);
    background-size: 200% 200%;
    background-position: bottom right;
    color: #4AD592;
    transition: all 0.25s ease;
    font-family: 'Baloo 2';
    &:hover {
        background-position: top left;
        color: rgb(19, 19, 19);
        background-color: #4AD592;
        color: whitesmoke;
 font-size: 20px;
       text-shadow: 1px 1px 3px black;
       font-weight: bold;
       font-family: 'Baloo 2';
    }
}

input[type=submit]{
    margin-top: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #4AD592;
    border:2px solid #4AD592;
    border-radius: 7px;
    height: 58px;
    width: 160px;
    background-color: #0F0F0F;
    font-size: 23px;
    font-weight: bold;
    transition: background-color 1.1s;
    &:hover{
        background-color: #4AD592;
        color: #0F0F0F;
}}

#contact_right{
    display: flex;
    flex-direction: column;
    color: wheat;
span{

    font-family: 'Baloo 2';
    font-weight: bold;
    margin-left: 16px;
}
}

.checkbox_p{
    display: flex;
    font-family: 'Montserrat';
    font-weight: bold;
width: 549px;
    img{
        height: 24px;
        margin-top: 12px;
        margin-right: 8px;
    }
}

@media (max-width:1275) {
    #contact_right{
     
        margin-left: 200px;
     }
   
}

@media (max-width:736px) {
    input[type=text],[type=email],select {
        width: 417px;
    }
    #message{ width: 417px;}
    #contact_right{
        justify-content: center;
        align-items: center;
    }
}

@media (max-width:463px){
    input[type=text],[type=email],select {
        width: 217px;
        height: 30px;
    }
    #message{ width: 217px;
    height: 200px;}
    .checkbox_p{
        font-size: 12px;
        margin-left: 10px;
        width: 240px;

        
    }
  

}

